html, body{
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: repeating-linear-gradient(135deg, rgba(32,32,32) 0%, rgb(30, 30, 30) .25%, rgba(32,32,32) .5%);
  background-attachment: fixed;


}

*{
  font-size: 24px;
  box-sizing: border-box;
  border-radius: 1px;
}

.match-parent{
  position: absolute;
  top: 0;
  left: 0;
  bottom:0;
  right: 0;
}

.transition-default{
  transition: all .25s ease-in;
}

.bkg-light{
  background:azure;
}

.font-light{
  color: azure;
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 30px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}
.app{
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

nav{
    position: fixed;
    display: flex;
    justify-content: center;
    width: 100%;
    background: rgba(30, 30, 30, .8);
    /* background: blue; */
    z-index: 999;
}

nav .menu-item-container{
    display: flex;
}

nav a{
    color: #f0ffff;
    text-decoration: none;
    cursor: pointer;
    padding: 0 1em;
    white-space: nowrap;
}

nav a .icon{
    display: flex;
    align-items: center;
}

nav a .marker{
    opacity: 0;
    transition: all .25s ease-in-out;
}

nav a:hover .marker{
    opacity: 1;
}

nav a i{
    visibility: hidden;
    width: 0;
    height: 0;
}

@media only screen and (max-width: 600px){
    nav a i{
        visibility: visible;
        width: 30px;
        height: auto;
    }
    nav a .menu-item-text{
        visibility: hidden;
        width: 0;
        height: 0;
    }
}

@media only screen and (max-width:400px){
    nav{
        justify-content: space-evenly;
    }
    nav a{
        padding: 0;
    }
}
.cover{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.welcome{
    flex-grow: 1;
    color: azure;
    display: flex;
    justify-content: center;
    align-items: center;
}

.welcome div{
    font-weight: 200;
    font-size: 2rem;
    max-width: 720px;
    text-align: center;
    margin: 3rem 1rem 0 1rem;
}

.name-container{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding-bottom: 2rem;
}

.name-container > .spacer{
    flex-grow: 1;
    min-width: 50vw;
}

.name{
    font-family: 'Tangerine', cursive;
    color: aliceblue;
    flex-grow: 1;
    /* border: 1px solid white; */
    padding-top: 2.5rem;
    display: flex;
    justify-content: center;
}

.name-text{
    font-size: 6rem;
    font-weight: 200;
    flex-grow: 0;
}

@media only screen and (max-width: 600px) {
    .name-text{
        font-size: 4rem;
    }
    .welcome div{
        font-size: 1.5rem;
    }
}

@media only screen and (max-width: 400px) {
    .name div{
        font-size: 2.75rem;
    }

    .underline{
        margin-top: .4rem;
    }
}

.name hr {
    margin-top: -1rem;
}
.about{
    display: flex;
    flex-direction: row;
    padding: 2rem 1rem 1rem 1rem;
}

.about div{
    flex-basis: 0px;
    flex-grow: 1;
}

.about-text{
    flex-grow: 4;
}

.about .spacer{
    max-width: 5vw;
}



.project{
    height: 50vh;
}

.project:nth-child(4n-2), .project:nth-child(4n-1){
    background:azure;
}

.project-grid{
    display:grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 2rem;
}

@media only screen and (max-width: 900px){
    .project-grid{
        grid-template-columns: 1fr;
    }
    .project:nth-child(even){
        background:azure;
    }
    .project:nth-child(odd){
        background:none;
    }
}

@media only screen and (max-width: 900px) and (orientation:landscape){
    .project{
        height: 100vh;
    }
}
.project{
    position: relative;
}

.project h1{
    margin: 0;
}

.project > div{
    margin: 2rem;
}

.project-image{
    display:flex;
    justify-content: center;
}

.overlay-container{
    display:flex;
    overflow: hidden;
}

.overlay{
    background: rgba(240, 255, 255, .7);
    padding: 1rem;
}

.description-container{
    flex-grow: 1;
}

.icon-container{
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    -webkit-writing-mode: vertical-lr;
            writing-mode: vertical-lr;
    flex-grow: 0;

}

.icon-container img{
    -webkit-writing-mode: horizontal-tb;
            writing-mode: horizontal-tb;
    width: 36px;
    height: auto;
    margin:.4em;
}

.description-container{
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.icon-container{
    -webkit-transform: translate(100%);
            transform: translate(100%);
}

.project:hover .overlay{
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

.project:hover .project-image{
    -webkit-transform: scale(1.025);
            transform: scale(1.025);
    -webkit-filter: blur(10px);
            filter: blur(10px);
}

@media only screen and (max-width: 900px) and (orientation:landscape){
    .icon-container img{
        width: 24px;
        height: auto;
        margin: .1rem;
    }

    .project > div{
        margin: 1.5rem;
    }
}

@media only screen and (max-width: 600px){
    .icon-container img{
        width: 24px;
        height: auto;
        margin: .1rem;
    }
    .description-container *{
        font-size: .75rem;
    }

    .project > div{
        margin: 1.5rem;
    }
}

@media only screen and (max-width:400px){
    .icon-container img{
        width: 20px;
        height: auto;
        margin: .1rem;
    }
    .description-container *{
        font-size: .67rem;
    }
    .project > div{
        margin: 1.25rem;
    }
}
.contact{
    display: flex;
    padding: 2.5rem 0;
    align-items: center;
}

@media only screen and (max-width: 900px){
    .contact{
        display: flex;
        flex-direction: column;
        padding: 2.5rem;
        align-items: center;
        justify-content:stretch;
    }

    .contact .spacer{
        max-width: 0;
        max-height: 0;
    }

    .contact .thanks{
        margin-bottom: 2rem;
        text-align: center;
    }
}

@media only screen and (max-width: 600px){
    .contact{
        display: flex;
        flex-direction: column;
        padding: 1.5rem;
        align-items: center;
        justify-content:stretch;
    }

    .contact .thanks{
        font-size: 1rem;
    }
}

.contact > div{
    flex-basis: 0px;
    /* border: 1px solid blue; */
}

.contact .spacer{
    max-width: 7rem;
    /* border: 1px solid red; */
    flex-grow: 1;
}

.contact-form-container{
    flex-grow: 0;
}

.thanks{
    font-size: 1.5rem;
    flex-grow: 8;
}

.contact-form{
    display:flex;
    flex-direction: column;
    min-width: 300px;
}

.form-text{
    border: none;
    border-bottom: 1px solid gray;
    background:none;
    margin: .25rem;
}

.form-button{
    border-radius: 999px;
    background: none;
    border: 1px solid gray;
}

.form-button:hover{
    background: hsl(180, 100%, 50%);
}

.form-button:active{
    background: hsl(180, 100%, 20%);
}

.footer-text{
    text-align: center;
    font-size: .5rem;
    padding: .5rem;
    
}
